// stolen from https://developer.mozilla.org/ru/docs/Web/JavaScript/Reference/Global_Objects/Math/ceil
function decimalAdjust(type, value, exp) {
  if (typeof exp === 'undefined' || +exp === 0) {
    return Math[type](value)
  }
  value = +value
  exp = +exp
  if (isNaN(value) || !(typeof exp === 'number' && exp % 1 === 0)) {
    return NaN
  }
  value = value.toString().split('e')
  value = Math[type](+(value[0] + 'e' + (value[1] ? +value[1] - exp : -exp)))
  value = value.toString().split('e')
  return +(value[0] + 'e' + (value[1] ? +value[1] + exp : exp))
}

export function adjustRoundPercentage(value, exp) {
  if (isNaN(value)) return '-'
  const percentage = decimalAdjust('round', value, exp) * 100
  return `${percentage.toFixed()}%`
}

export function toPercentageValue(value) {
  const int = parseInt(value)
  return isNaN(int) || int === 0 ? '-' : `${int}%`
}

export function toFloorPercentageValue(value) {
  const float = parseFloat(value)
  return isNaN(float) || float === 0 ? '-' : `${float}%`
}
